  text-align: center;
  position: absolute;
  top: 3.4em;
  left: 0;
  width: 100%;
  height: 100%;

}


.tabs {
  background: linear-gradient(to bottom, #414345, #414345);
}

.date-timeline p {

  color: white;
  font-family: "Courier New", Courier, "Lucida Sans Typewriter", "Lucida Typewriter", monospace;
  font-style: normal;
  -webkit-font-feature-settings: normal;
          font-feature-settings: normal;
  font-variant: normal;
}
.avatar-img {
  height:200px;
  padding-top: 5em;
}

.banner-text {
  background-color: #489ac1;
  opacity: .5;
  width: 75%;
  margin: Auto;
  border-radius: 10px;
}

.banner-text h1 {
  font-size: 66px;
  font-weight: bold;
  color: white;
  font-family: "Courier New", Courier, "Lucida Sans Typewriter", "Lucida Typewriter", monospace;
  font-style: normal;
  -webkit-font-feature-settings: normal;
          font-feature-settings: normal;
  font-variant: normal;
  line-height: 69.4px;
}

.banner-text hr {
  border-top: 5px dotted white;
  width: 50%;
  margin: auto;

}

.banner-text p {
  color: white;
  font-size: 20px;
  padding: 1em;
  font-family: "Courier New", Courier, "Lucida Sans Typewriter", "Lucida Typewriter", monospace;
  font-size: 24px;
  font-style: normal;
  -webkit-font-feature-settings: normal;
          font-feature-settings: normal;
  font-variant: normal;
  font-weight: 700;
  line-height: 26.4px;
}

.social-links {
  display: -webkit-flex;
  display: flex;
  -webkit-justify-content: space-between;
          justify-content: space-between;
  width: 50%;
  margin: auto;
}

.social-links i {
  color: white;
  font-size: 5em;
  padding: 8px;
}

.namer {
  position: relative;
  max-width: 400px;
  margin: 5px auto 0;
}

.namer input {
  border: 0;
  border-bottom: 2px solid white;
  width: 68%;
  font-size: 20px;
  line-height: 55px;
  height: 55px;
  text-align: center;
  padding: 10px;
  background: transparent;
  color: white;
  opacity: 0.8;


}

.namer input.shake {
  -webkit-animation-name: shaker;
          animation-name: shaker;
  -webkit-animation-duration: 200ms;
          animation-duration: 200ms;
  -webkit-animation-timing-function: ease-in-out;
          animation-timing-function: ease-in-out;
  -webkit-animation-delay: 0s;
          animation-delay: 0s;
}

.namer input:focus {
  outline: 0;
  color: white;
  font-family: "Courier New", Courier, "Lucida Sans Typewriter", "Lucida Typewriter", monospace;
  font-size: 15px;
  font-style: normal;
  -webkit-font-feature-settings: normal;
          font-feature-settings: normal;
  font-variant: normal;
  font-weight: 500;
}

.namer input::-webkit-input-placeholder {
  color: white;
  font-family: "Courier New", Courier, "Lucida Sans Typewriter", "Lucida Typewriter", monospace;
  font-size: 15px;
  font-style: normal;
  -webkit-font-feature-settings: normal;
          font-feature-settings: normal;
  font-variant: normal;
  font-weight: 500;

}

.namer input::-ms-input-placeholder {
  color: white;
  font-family: "Courier New", Courier, "Lucida Sans Typewriter", "Lucida Typewriter", monospace;
  font-size: 15px;
  font-style: normal;
  font-feature-settings: normal;
  font-variant: normal;
  font-weight: 500;

}

.namer input::placeholder {
  color: white;
  font-family: "Courier New", Courier, "Lucida Sans Typewriter", "Lucida Typewriter", monospace;
  font-size: 15px;
  font-style: normal;
  -webkit-font-feature-settings: normal;
          font-feature-settings: normal;
  font-variant: normal;
  font-weight: 500;

}

.namer-controls {
  position: relative;
  display: block;
  height: 30px;
  margin: 20px 0;
  text-align: center;
  opacity: 0.3;
  cursor: not-allowed;
}

.namer-controls.active {
  opacity: 1;
  cursor: pointer;
}

.namer-controls div {
  float: left;
  width: 33.33%;
}

.namer-controls div span {
  box-shadow: 0px 0px 30px 0px rgba(0, 0, 0, 0.25);
  padding: 10px 5px;
  width: 95%;
  display: inline-block;
  margin-right: 5%;
  border-radius: 30px;
  font-size: 15px;
  text-transform: uppercase;
  letter-spacing: 0.3px;
}

.namer-controls div span:last-child {
  margin-right: 0;
}

.namer-controls div span.active {
  box-shadow: none;
  background-color: #1976D2;
  color: #fff;
}

#namer-input.serious input {
  letter-spacing: 2px;
  text-transform: uppercase;
  font-family: 'Andada', serif;
  font-weight: 500;
}

.namer-input.modern input {
  font-family: 'Raleway', sans-serif;
  text-transform: lowercase;
  font-weight: 300;
  letter-spacing: 10px;
}

.resume-grid {
  background: #232526;  /* fallback for old browsers */
  /*background: -webkit-linear-gradient(to right, #414345, #ff396e);  !* Chrome 10-25, Safari 5.1-6 *!*/
  background: linear-gradient(to bottom, #414345, #57899c); /* W3C, IE 10+/ Edge, Firefox 16+, Chrome 26+, Opera 12+, Safari 7+ */

  text-align: center;
  position: absolute;
  top: 0em;
  left: 0;
  width: 100%;
  height: 100%;

}

.namer-input.cheeky input {
  font-family: 'Permanent Marker', cursive;
  font-size: 40px;
}

@-webkit-keyframes shaker {
  0% {
    -webkit-transform: translate(0px, 0px) rotate(0deg);
            transform: translate(0px, 0px) rotate(0deg);
    opacity: 0.8;
  }
  10% {
    -webkit-transform: translate(10px, 7px) rotate(-9deg);
            transform: translate(10px, 7px) rotate(-9deg);
    opacity: 0.6;
  }
  20% {
    -webkit-transform: translate(13px, -19px) rotate(-3deg);
            transform: translate(13px, -19px) rotate(-3deg);
    opacity: 0.3;
  }
  30% {
    -webkit-transform: translate(-6px, -6px) rotate(2deg);
            transform: translate(-6px, -6px) rotate(2deg);
    opacity: 0.4;
  }
  40% {
    -webkit-transform: translate(-9px, -18px) rotate(-5deg);
            transform: translate(-9px, -18px) rotate(-5deg);
    opacity: 0.4;
  }
  50% {
    -webkit-transform: translate(10px, -8px) rotate(5deg);
            transform: translate(10px, -8px) rotate(5deg);
    opacity: 0.7;
  }
  60% {
    -webkit-transform: translate(-10px, 14px) rotate(-6deg);
            transform: translate(-10px, 14px) rotate(-6deg);
    opacity: 1;
  }
  70% {
    -webkit-transform: translate(10px, 3px) rotate(6deg);
            transform: translate(10px, 3px) rotate(6deg);
    opacity: 0.1;
  }
  80% {
    -webkit-transform: translate(-2px, 20px) rotate(-6deg);
            transform: translate(-2px, 20px) rotate(-6deg);
    opacity: 1;
  }
  90% {
    -webkit-transform: translate(-7px, -19px) rotate(2deg);
            transform: translate(-7px, -19px) rotate(2deg);
    opacity: 0.5;
  }
}

@keyframes shaker {
