  }
  40% {
    -webkit-transform: translate(-9px, -18px) rotate(-5deg);
            transform: translate(-9px, -18px) rotate(-5deg);
    opacity: 0.4;
  }
  50% {
    -webkit-transform: translate(10px, -8px) rotate(5deg);
